import { Injectable } from '@angular/core';
import { Headers, Http,RequestOptions,ResponseContentType } from '@angular/http';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';
import 'rxjs/add/operator/toPromise';

@Injectable()
export class FileService {

  private filesUrl = '/api/v1/file/listing';  // URL to web api
  private createDirectoryUrl = '/api/v1/directory/create';  // URL to web api
  private headers = new Headers({ 'Content-Type': 'application/json'});
  private options = new RequestOptions({ headers: this.headers, responseType: ResponseContentType.Blob});
  private baseDir = "managed-care";
  constructor(private http: Http) {}
  getFiles(url: string): Promise<any> {
    return this.http
      .post(url, JSON.stringify({}), {})
      .toPromise()
      .then()
      .catch();
  }
}